* {
    box-sizing: border-box; }

@font-face {
    font-family: 'Philosopher';
    src: local('Philosopher');
    src: url(../font/Philosopher-Regular.ttf); }

html {
    font-family: 'Roboto!'; }

@font-face {
    font-family: 'Roboto!';
    src: local('Roboto');
    src: url('../font/Roboto-Regular.eot');
    src: url('../font/Roboto-Regular.eot?#iefix') format('embedded-opentype'),
    url('../font/Roboto-Regular.woff') format('woff'),
    url('../font/Roboto-Regular.ttf') format('truetype') {}
    font-weight: 400;
    font-style: normal; }

@font-face {
    font-family: 'Roboto!';
    src: url('../font/Roboto-Bold.eot');
    src: url('../font/Roboto-Bold.eot?#iefix') format('embedded-opentype'),
    url('../font/Roboto-Bold.woff') format('woff'),
    url('../font/Roboto-Bold.ttf') format('truetype') {}
    font-weight: 700;
    font-style: normal; }

body {
    background-color: #ffe2b0; }

header {
    text-align: center;
    margin-bottom: 20px; }

ul {
    list-style-type: none;
    text-align: center;
    padding: 0px; }

li {
    display: inline;
    margin-right: 100px; }

a {
    color: black;
    text-decoration: none;
    font-size: 18px;
    font-weight: 700; }

li:last-child {
    margin-right: 0px; }

nav {
    margin-bottom: 20px; }

.section-about {
    height: 650px;

    position: relative;
    margin-bottom: 20px; }

.carousel-item {
    position: relative;
    img {
        height: 650px;
        object-fit: cover; } }

.content {
    position: absolute;
    max-width: 600px;
    left: 13%;
    bottom: 20%;
    z-index: 2;
    color: white; }

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.4; }

.section-about__block {
    width: 420px;
    height: 190px;
    background: linear-gradient(180deg, rgba(244, 213, 178, 0.8) 0%, rgba(234, 156, 65, 0.8) 100%);
    position: absolute;
    top: 54%;
    left: 50%;
    text-align: center;
    padding-top: 15px; }

.section-about__border {
    width: 390px;
    height: 160px;
    border: 1px solid black;
    margin: 0 auto;
    padding: 17px 0 0 28px;
    text-align: left; }

.section-about__title {
    font-size: 36px;
    margin: 0;
    margin-bottom: 9px;
    font-family: 'Philosopher'; }

.section-about__text {
    font-size: 12px;
    margin: 0; }

.section-description {
    margin-bottom: 20px; }

.section-description__icon-box {
    text-align: center;
    margin-bottom: 20px; }

.section-description__icon-box img {
    margin-right: 76px; }

.section-description__icon-box img:last-child {
    margin-right: 0px; }

.section-description__title {
    font-size: 96px;
    margin: 0pc;
    text-align: center;
    font-family: 'Philosopher'; }

.red {
    color: #f15b40; }

.section-description__text {
    font-size: 18px;
    margin: 0px;
    text-align: center;
    width: 1000px;
    margin: 0 auto; }

.section-map {
    margin-bottom: 20px; }

.section-map__title {
    margin-bottom: 20px;
    text-align: center;
    font-size: 36px;
    font-family: 'Philosopher'; }

iframe {
    display: block;
    margin: 0 auto; }

hr {
    border: none;
    border-top: 1px solid #999999;
    width: 1024px;
    margin: 0 auto;
    margin-bottom: 20px; }

footer {
    height: 80px; }

.footer-ul {
    margin-bottom: 50px; }

.footer-ending-text {
    font-size: 12px;
    margin: 0px;
    text-align: center;
    width: 1000px;
    margin: 0 auto;
    height: 40px; }
